import read from './read';

/**
 * json
 *
 * @param jsonSet json
 * @param classReference 
 */
export default function readSet<T>(
  jsonSet: Set<any>,
  classReference: { new (): T },
): Set<T> {
  const set: Set<T> = new Set<T>();
  jsonSet.forEach((value) => set.add(read(value, classReference)));
  return set;
}
