import read from './read';

/**
 * json
 *
 * @param jsonMap json
 * @param classReference 
 */
export default function readMap<K, V>(
  jsonMap: Map<K, V>,
  classReference: { new (): V },
): Map<K, V> {
  const result: Map<K, V> = new Map<K, V>();
  jsonMap.forEach((value: V, key: K) => {
    result.set(key, read(value, classReference));
  });
  return result;
}
